.heroContainer {
    background: #0c0c0c;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    height: 100vh;
    position: relative;
    z-index: 1;

    // :before {
    //     content: '';
    //     position: absolute;
    //     top: 0;
    //     left: 0;
    //     right: 0;
    //     bottom: 0;
    //     background: linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.6)  100%), 
    //                 linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 100%);
    //                 z-index: 2;
    // }

    &__background {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;

    }

    &__content {
        z-index: 3;
        max-width: 1200px;
        position: absolute;
        padding: 8px 24px;
        display: flex;
        flex-direction: column;
        align-items: center;

        h1 {
            color: #ffffff;
            font-size: 48px;
            text-align: center;

            @media screen and (max-width: 768px) {
                font-size: 40px;
            }

            @media screen and (max-width: 480px) {
                font-size: 32px;
            }
        }

        p {
            margin-top: 24px;
            color: #ffffff;
            font-size: 24px;
            text-align: center;
            max-width: 600px;

            @media screen and (max-width: 768px) {
                font-size: 24px;
            }

            @media screen and (max-width: 480px) {
                font-size: 18px;
            }
        }

        &--wrapper{
            margin-top: 32px;
            display: flex;
            flex-direction: column;
            align-items: center;

        }
    }
}